import React from "react";
import technology from "../../styles/assets/images/FramworksHD/technology.png";
import react from "../../styles/assets/images/FramworksHD/React.png";
import angular from "../../styles/assets/images/FramworksHD/Angular.png";
import js from "../../styles/assets/images/FramworksHD/js.png";
import solidity from "../../styles/assets/images/FramworksHD/solid.png";
import node from "../../styles/assets/images/FramworksHD/javascript.svg";
import mongodb from "../../styles/assets/images/FramworksHD/mongodb.svg";

const OurDevelopment = () => {
  return (
    <section className="section-tech py-5">
      <div className="container">
        <div className="row d-flex align-items-center justify-content-center">
          <h3 className="text-center text-uppercase">
            Our Development Technologies
          </h3>
        </div>
        <div className="row">
          {/* <div className="col-md-6 col-sm-12 col-12 d-flex flex-column justify-content-center align-items-center">
            <img className=" w-75" src={technology} alt="technology-img" />
          </div> */}
          <div className="col-md-6 p-5 col-sm-12 col-12 m-auto">
            <div className="row d-flex justify-content-center align-items-center">
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img
                    className=" techstack-img"
                    src={react}
                    alt={"react-img"}
                  />
                  <figcaption className="">React</figcaption>
                </figure>
              </div>
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img
                    className="techstack-img"
                    src={angular}
                    alt="angular-img"
                  />
                  <figcaption className="">Angular</figcaption>
                </figure>
              </div>
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img className=" techstack-img w-90" src={js} alt="js-img" />
                  <figcaption className="">Express JS</figcaption>
                </figure>
              </div>
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img
                    className=" techstack-img"
                    src={solidity}
                    alt="solidity-img"
                  />
                  <figcaption className="">Solidity</figcaption>
                </figure>
              </div>
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img className=" techstack-img" src={node} alt="node-img" />
                  <figcaption className="">Node JS</figcaption>
                </figure>
              </div>
              <div className="col-md-4 col-sm-4 col-4">
                <figure className="gallery-item">
                  <img
                    className=" techstack-img"
                    src={mongodb}
                    alt="mongodb-img"
                  />
                  <figcaption className="">Mongodb</figcaption>
                </figure>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default OurDevelopment;
