import React from "react";
import { Container } from "react-bootstrap";
import partzrider from "../../styles/assets/images/recentworks/partzrider.png";
import truefriend from "../../styles/assets/images/recentworks/tfm.png";
import uttimo from "../../styles/assets/images/recentworks/uttimo.png";

const RecentWorks = () => {
  return (
    <Container>
      <section className="recentworks">
        <div className="row">
          <h3 className="text-center text-uppercase">our recent works</h3>
        </div>
        <div className="row p-5">
          <div className="col-md-5 d-flex align-items-center justify-content-center">
            <img
              src={partzrider}
              className="rightAnimation work-img"
              alt={"partzrider-img"}
            />
          </div>
          <div className="col-md-6">
            <h4 className="heading-fourth subhead">Partzrider</h4>
            <p className="description">
              The portal, Partzrider.com is owned and operated by George Oakes
              Limited, a public company incorporated under the Companies Act,
              1913. George Oakes enables sale and purchase of diverse range of
              products listed on the Platform from time to time.
            </p>
            <a
              target="_blank"
              className="custom-btn btn--full"
              href="https://partzrider.com/"
              rel="noreferrer "
            >
              Visit
            </a>
          </div>
        </div>
        <div className="row p-5">
          <div className="col-md-5 py-5 d-flex align-items-center justify-content-center">
            <img
              src={truefriend}
              className="rightAnimation work-img"
              alt={"truefriend-matrimony-img"}
            />
          </div>
          <div className="col-md-6 d-flex flex-column justify-content-center align-items-start">
            <h4 className="heading-fourth servicehead">Truefriend Matrimony</h4>
            <p className="description">
              True Friend Matrimony, a trusted platform to find your life
              partner. Our website is dedicated to helping you find a compatible
              match who shares your faith, values, and aspirations, and will be
              a great and perfect match for your beloved family.
            </p>
            <a
              target="_blank"
              className="custom-btn btn--full"
              href="https://www.truefriendmatrimony.com/"
              rel="noopener"
            >
              Visit
            </a>
          </div>
        </div>
        <div className="row p-5">
          <div className="col-md-5 py-5 d-flex align-items-center justify-content-center">
            <img
              src={uttimo}
              className="rightAnimation work-img"
              alt="recentworks logo"
            />
          </div>
          <div className="col-md-6  d-flex flex-column justify-content-center align-items-start">
            <h4 className="heading-fourth servicehead">Uttimo</h4>
            <p className="description">
              Uttimo is India's leading online marketplace for auto spare parts.
              With an extensive catalog of genuine and aftermarket auto
              components, it connects customers to a vast network of suppliers,
              ensuring quick and reliable access to quality parts for all types
              of vehicles. Uttimo simplifies repairs and maintenance, offering a
              seamless shopping experience.
            </p>
            <a
              target="_blank"
              rel="noopener"
              className="custom-btn btn--full"
              href="https://dev.uttimo.com/"
            >
              Visit
            </a>
          </div>
        </div>
      </section>
    </Container>
  );
};

export default RecentWorks;
